import * as React from 'react';
import { useSearchParams } from 'react-router-dom';

import Staff from '@miq/staffjs';

import { useRequest, ResultsResponse } from '@miq/hookjs';
import { Pagination, Table } from '@miq/componentjs';

import { Insight, TInsight } from './utils';

export default function StoreInsightsCategoryView({ storeSlug }: { storeSlug: string }) {
  const [params] = useSearchParams();
  const req = useRequest(() => Insight({ slug: storeSlug }).categories(params), { refreshDeps: [params.get('page')] });
  const res = new ResultsResponse(req.res);
  if (!res.isSuccess) return null;

  return (
    <Staff.View
      text={`Top categories: ${res.data?.count}`}
      footer={<Pagination {...res.pagination} component={Staff.Link} to />}
    >
      <Table<TInsight>
        items={res.items}
        header={
          <>
            <Table.Th></Table.Th>
            <Table.Th>Unique</Table.Th>
            <Table.Th>Total</Table.Th>
            <Table.Th>External</Table.Th>
          </>
        }
        renderItem={(item) => {
          return (
            <Table.Tr key={item.path}>
              <Table.Td className="d-grid">
                <div className="text-ellipsis">{item.path}</div>
              </Table.Td>
              <Table.Td>{item.ip_count}</Table.Td>
              <Table.Td>{item.id_count}</Table.Td>
              <Table.Td>{item.x_count}</Table.Td>
            </Table.Tr>
          );
        }}
      />
    </Staff.View>
  );
}
