import { StaffModel, StaffService } from '@miq/staffjs';
import { URLSearchParamsInit } from 'react-router-dom';

export type TInsight =
  | { path: string; ip_count: number; msg_count: number; id_count: number; x_count: number }
  | { slug: string }
  | {};

class InsightService extends StaffService<TInsight> {
  products(params?: URLSearchParamsInit) {
    return this.get(`${this._path}products/`, { params });
  }
  categories(params?: URLSearchParamsInit) {
    return this.get(`${this._path}categories/`, { params });
  }
  hits(params?: URLSearchParamsInit) {
    return this.get(`${this._path}hits/`, { params });
  }
}

export const Insight = (data: TInsight = {}) => {
  return StaffModel<TInsight, InsightService>(data, 'shopy-insights', InsightService);
};

export type TInsightInstance = ReturnType<typeof Insight>;

export type TInsightOrInstance = TInsight | TInsightInstance;
